import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DataTableModule } from "angular2-datatable";

import { UserregistrationComponent } from './userregistration.component';
import { UserregistrationRoutes } from './userregistration.routing';
import { DataFilterPipe }   from './data-filter.pipe';

@NgModule({
    imports: [
        RouterModule.forChild(UserregistrationRoutes),
        CommonModule,
        DataTableModule,
        FormsModule
    ],
    declarations: [UserregistrationComponent, DataFilterPipe]
})

export class UserregistrationModule {}


